import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import Signup from "./views/Signup.vue";
import Login from "./views/Login.vue";
import Logout from "./views/Logout.vue";
import Trips from "./views/TripsIndex.vue";
import Trip from "./views/TripsShow.vue";
import Attractions from "./views/AttractionsIndex.vue";
import Attraction from "./views/AttractionsShow.vue";
import Map from "./views/test.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/about",
      name: "about",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ "./views/About.vue")
    },
    {
      path: "/signup",
      name: "signup",
      component: Signup
    },
    {
      path: "/login",
      name: "login",
      component: Login
    },
    {
      path: "/logout",
      name: "logout",
      component: Logout
    },
    {
      path: "/trips",
      name: "trips-index",
      component: Trips
    },
    {
      path: "/trips/:id",
      name: "trips-show",
      component: Trip
    },
    {
      path: "/attractions",
      name: "attractions-index",
      component: Attractions
    },
    {
      path: "/attractions/:id",
      name: "attractions-show",
      component: Attraction
    },
    {
      path: "/test",
      name: "map",
      component: Map
    }
  ]
});
